<template lang="pug">
  button.button(
      v-bind:class="classList()"
    )
    .label {{label}}
</template>

<script>
export default {
  name:'Button',
  props:['label','type','fuild','disabled'],
  methods:{
    classList(){
      const { type, fuild, disabled } = this
      return {
        fuild:fuild,
        primary:!type || type == 'primary',
        disabled:disabled
      }
    }
  }
}
</script>

<style lang="scss">
.button{
    outline: none;
    font-weight: 500;
    font-family: 'Muller',sans-serif;
    display: flex;
    cursor: pointer;
    user-select: none;
    font-size: 16px;
    letter-spacing: 1px;
    border: none;
    color: white;
    padding-top:  12px;
    padding-bottom: 10px;
    min-width:  150px;
    border-radius: 3px;
    box-shadow: inset 0px -1px 0px 1px rgba(0,0,0,0.2);
    padding-right: 6px;
    padding-left: 6px;
    align-items: center;
    justify-content: center;
    align-content: center;
    transition: 0.3s all;
    &.primary{
      background-color: #EA600F;
      &:hover,&:focus{
        background-color: #FD985D;
      }
    }
    &.warning{
      background-color: #FC5830;
      &:hover,&:focus{
        background-color: #FD7858;
      }
    }
    &.disabled{
      background-color: #8E8E98;
      cursor: no-drop;
      &:hover,&:focus{
        background-color: #6f6f79;
      }
    }
    &.fuild{
      width: 100%;
    }
  }
</style>



