import Finminer from '@/services/finminer'
import storage from '@/storage'
import moment from 'moment'

const temperaturesFilter = devices => devices.filter(
   rig => {
    const hour = 1000 * 60 * 60
    const now = new Date()
    const time = new Date(rig.time)
    return now - time < 12*hour
  }
)

const storeageDevices = storage.getItem('devices') || []
const initialDevices = storeageDevices.map(device=>{
  device.temperatures = temperaturesFilter(device.temperatures)
  return device
})


export default {
  namespaced:true,
  state:{
    currencies:[],
    // devices:initialDevices,
    list:[],
    last:{},
    info:{},
    devices:[]
  },
  mutations:{
    last(state,data){
      state.last = data
    },
    list(state,data){
      state.list = data || []
    },
    info(state,information){
      state.info = information
    },
    devices(state,devices){
      state.devices = devices
    },
    currencies(state,currencies){
      state.currencies = currencies
    }
  },
  actions:{
    last({commit}){
      Finminer.statistics.last()
        .then(data=>commit('last',data))
        .catch(e=>console.error(e))
    },
    loading({commit}){
      const { current, list } = Finminer.statistics
      current().then( data => {
        commit('current',data)
      })
      list().then( data => {
        commit('list',data)
      })
    },
    list:({commit})=>Finminer.statistics.list()
      .then( data => {
        commit('list',data)
        return data
      }),
    devices:({commit})=>{
      Finminer.statistics.current()
        .then( data => {
          commit('devices',data.Devices)
          commit('currencies',data.Currencies)
          return data
        })
    }
}
}



