<template lang="pug">
page.notFound
  page-main

    //- img.logo(src="@/assets/icons/not-found.svg")
    .content
      div
        h1 {{ $route.params.title || title || '404' }}
      div
        p {{ $route.params.descripton || descripton || 'Sorry... not found this page.' }}
</template>

<script>
export default {
  props:['title','descripton'],
  created(){
    if(!this.title)
      this.$store.commit('app/subtitle',{
        title:'NOT FOUND',
      })
  }
}
</script>

<style lang="scss">
 .notFound{
   .logo{
    width:100px;
    margin-right:40px;
   }
  .page__main{
    display: flex;
    justify-content: center;
    align-items: space-between;
    min-height: 40vh;
  }
  .content{
    display: flex;
    justify-content: center;
    //- align-items: ;
    min-height: 50vh;
    flex-direction: column;
    color:#B0B3C1;
    font-weight:400;
    font-size:16px;
    h1{
      text-transform:uppercase;
      font-size:34px;
      color:#B0B3C1;
      letter-spacing:1px;
      margin-top: 0px;
      margin-bottom: 20px;
    }
  }
 }
</style>



