import statistic from './statistic'
import miners from './miners'
import session from './session'
import settings from './settings'
export default {
  namespaced:true,
  state: {
    loading:false,
    notification:{},
  },
  mutations: {
    notification(state,data){
      state.notification = data
    }
  },
  getters:{

  },
  actions:{
    notification(store,payload){
      console.log('App:',payload.text)
      store.commit('notification',payload)
    },
    synchronization({dispatch}){
      const loading = (actions)=> 
        actions.forEach(namespace=>
          dispatch(namespace,null,{root:true})
        )

      loading([
        'app/statistic/last',
        'app/miners/scanning',
        'app/miners/log',
        'app/miners/config',
        'app/statistic/devices',
      ])

    },
  },
  modules:{
    statistic,
    miners,
    session,
    settings
  }
}



