<template lang="pug">
  .app-bar__navigation()
    Button(label="Restart miner" :disabled="!actived || password == ''" @click.native="command('restart')")
    Button(label="Show log" @click.native="$modal.show('log')")
</template>
<script>
import { createNamespacedHelpers } from 'vuex'
const SessionStore  = createNamespacedHelpers('app/session')
const MinersStore  = createNamespacedHelpers('app/miners')
  export default {
    computed:{
      ...SessionStore.mapState(['actived']),
      ...SessionStore.mapState(['password']),
    },
    methods:{
      ...MinersStore.mapActions(['commands']),
      command(name){
        if (this.$store.state.app.session.password.length && this.$store.state.app.session.actived) {
          // otherwise it bugs and processes the command
          this.commands({command:name})
        }
      },
    }
  }
</script>
<style lang="scss">

.app-bar__navigation{
  display: flex;
  justify-content: flex-start;
  align-items: center;

  button{
    margin-right: 10px
  }
  &:last-child{
    margin-right: 0px;
  }
}
</style>



