<template lang="pug">
  li.list-item
    .list-item__name(v-if="name || $slots.name ")
     | {{name}}
     slot(name="name")
    .list-item__value(v-if="value !==undefined || $slots.value" v-bind:class="{'text-overflow':overflow}"  v-bind:style="{ width: overflow?overflow+'px':'auto' }")
      slot(name="value")
      | {{value}}

    slot(name="actions")
</template>

<script>
export default {
  created(){
  },
  props:['name','value','actions','overflow'],
}
</script>

<style lang="scss">
  .list-item{
    display: flex;
    align-items:flex-start;
    justify-content: space-between;
    text-decoration: none;
    list-style: none;
    text-align: left;
    letter-spacing: 0.2pt;

    padding-bottom: 3px;
    margin-bottom:9px;
    border-bottom: 1px dashed #EAEBEF;
    &:last-child{
      margin-bottom: 0px;
    }
    &__name{
      display: flex;
      color:#96969E;
      margin-right: 10px;
      font-weight: 500;
    }
    &__value{
      color:#525252;
      margin: 0px;

    }
  }
</style>



