import * as screens from '@/screens'
import notFound from '@/components/not-found'
import Store from '@/store'
export default [
  {
    name:'mining',
    path: '/',
    component:screens.mining,
    async beforeEnter(to,from,next){
     next()
    }
  },
  {
    name:'rig',
    path: '/rig',
    component:screens.rig,
    async beforeEnter(to,from,next){
      next()
    }
  },
  {
    name:'settings',
    path:'/settings',
    component:screens.settings,
    async beforeEnter(to,from,next){
     await Store.dispatch('app/miners/config')
      next()
    }
  },
  {
    name:'scanning',
    path:'/scanning',
    component:screens.list,
    async beforeEnter(to,from,next){
      next()
    }
  },
  {
    path:'not-found',
    name:'not-found',
    component:notFound
  },
  {
    path:'*',
    component:notFound
  },
]



