<template lang="pug">
  page.rig
    page-header( )
      card.stats(title="Rig")
        card.paper.a(v-if="last.devices")
          chart.chart(:data="last.devices")


    page-main
      card

        table-manager(:props="table" :data="devices")
          template(slot="hashrates" scope="{item}")
            div(v-for="currency in item") {{ currency.coin+': '+currency.hashrate+' '+currency.unit+' ('+currency.gpuAccepted+' accepted, ' + currency.gpuDenied + ' rejected)' }}
          template(slot="memorySizeMB" scope="{item}") {{item+' mb'}}
</template>

<script>
import { mapState } from 'vuex'
import chart from './chart'

export default {
  components:{
    chart
  },
  created(){
  },
  data:()=>({
    table:{
      headers:{
        GPU:'id',
        NAME:'name',
        HASHRATE:'hashrates',
        TEMPERATURE:'temp',
        PLATFORM:'platform',
        MEMORY:'memorySizeMB',
      },
    }
  }),
  computed:mapState({
    devices:state => { return state.app.statistic.devices ? state.app.statistic.devices.filter(item => item.id !== "CPU") : state.app.statistic.devices},
    last:state => state.app.statistic.last,
  }),
}
</script>

<style lang="scss">
.page.rig{
  .chart{
    width: 100%;
    height: 300px;

  }
  .card.stats{
    width: 100%;
    overflow-y: auto;
    .card__main{
      display: flex;
      justify-content: space-between;
      .list{
        width: 100%;
      }
    }

    .card.a{

      flex-basis: 100%
    }

  }
  .page__main
    .card{
      width: 100%;
    }
}
</style>



