<template lang="pug">
  .table-container
    transition(  name="component-fade" mode="out-in")
      .table-container(key="table" v-if=" !isEmpty()")
        table.table
          tbody
            tr.table-header
              th.table-header__item(
                v-for="(header,key) in props.headers"
              ) {{ key }}

            tr.table-row(v-if="!isEmpty()" v-for="(item,index) in props.data")
              td(v-for="(header,key) in props.headers")
                .table-item(v-if="item" :style="{opacity:props.loading?'0.5':'1'}")
                  slot(
                    v-if="$scopedSlots[header.key?header.key:header]"
                    :name="header.key?header.key:header"
                    :item = "item[header.key?header.key:header]"
                    :props = "item"
                    :header = "key"
                    :_key="header"
                    :index="index"
                  )
                  slot(
                    v-if="$scopedSlots.default && !$scopedSlots[header.key?header.key:header]"
                    :item = "item[header.key?header.key:header]"
                    :props = "item"
                    :header = "key"
                    :_key="header"
                    :index="index"
                  )
                  span(v-if="!$scopedSlots[header.key?header.key:header] && !$scopedSlots.default") {{item[header.key?header.key:header]}}

        Pagination.VuePagination__container(
          key="pagination"
          ref="pagination"
          v-if="!isEmpty() && props.paginate"
          :records="props.records || props.count"
          :per-page="props.perpage || props.limit"
          @paginate="props.paginate"
        )
      .actions(key="desc" v-if="isEmpty() || props.loading && isEmpty()")
        transition(  name="component-fade" mode="out-in")
          .table__loading(key="loading" v-if="props.loading && isEmpty()")
            //- img(src="@/assets/icons/loading.svg").loading
          .table__empty(key="empty" v-if="!props.loading && isEmpty()")
            //- notFound(title="empty" descripton="In this table there is no data yet")
</template>
<script>
import {Pagination} from 'vue-pagination-2';
import notFound from '@/components/not-found'
const initial = ()=>({
  loading:false,
  offset:0,
  limit:10,
  page:1,
  count:0,
  data:[],
  headers:{},
})

export default {
  props:[
    'props',
    'data',
  ],
  components:{
    Pagination,
    notFound
  },

  created(){
    const init = initial()

    for (var prop in init) {
      if(!this.props.hasOwnProperty(prop))
        this.props[prop] = init[prop]
    }

    if(this.data)
      this.props.data = this.data
  },
  watch:{
    data(data){
      this.props.data = data
    }
  },

  mounted(){
    if(this.$refs.pagination && this.page && !this.loading)
      this.$refs.pagination.setPage(this.page)
  },
  methods:{
    onPaginate(page) {
      if(this.props.page == page) return // not reload
      const { offset, limit } = this.props
      if(page && limit){
        this.props.page = page;
        this.props.offset = page*limit
        this.props.loading = true
      }
      this.onWallets({collection})
    },
    loadingSuccess({ offset, limit, page, count }) {
      if(!page) return { loading:false }
      return {
        loading:false,
        ofset: offset + limit,
        limit: offset + limit < count ? limit : offset + limit - count,
      }
    },
    isEmpty(){
      if(!this.props.data) return true
      return this.props.data.length == 0
    }
  }
}
</script>

<style lang="scss">
  .table-container{
    position: relative;
    min-height: 40vh;
    width: 100%;
  }
  table.table{
    width: 100%;

    border-collapse: collapse;
    tbody{
      tr.table-header{
        &>th{
          font-weight: 500;
          user-select: none;
          white-space: nowrap;
          text-align: left;
          font-size: 12px;
          letter-spacing: 1pt;
          text-transform: uppercase;
          color:rgba(82, 82, 82, 0.8);
          background-color: white
        }
      }
      tr{
        &>th, td{
          padding: 18px 8px 18px 8px;
          &:first-child{
            padding-left:  18px;
          }
           &:last-child{
             padding-right:  18px;
           }
        }
        &:nth-child(odd){
          overflow: hidden;
          border-radius: 8px;
          background-color: rgba(255,255,255,0.4);
          box-shadow: 0px 0px 0px 1px #f1f1f1;
         &>th, td{
           background-color: rgba(255,255,255,0.4);
           &:first-child{
             border-top-left-radius: 8px;
             border-bottom-left-radius: 8px;
             overflow: hidden;
           }
           &:last-child{
             border-top-right-radius: 8px;
             border-bottom-right-radius: 8px;
             overflow: hidden;
           }
         }
         &:first-child {
           box-shadow: 0px 0px 0px 1px #EAEBEF !important;
           &>th{
            background-color: rgb(255,255,255) !important;
          }
         }
        }
      }
    }
  }
  .table{
    &-header{
      &__item{

      }
    }
      &__loading,&__empty{
        .loading{
          width: 100px;
        }
        width: 100%;
        user-select: none;
        min-height: 50vh;
        display: flex;
        justify-content: center;
        align-items: center;
        align-content: center;
        color:#a9a9b0;
        border-radius: 8px;
        box-shadow: 0px 0px 0px 1px #f1f1f1;
        background-color: rgba(255,255,255,0.4);
      }
    }
.table-item{
  white-space: nowrap;
  transition: 0.3s;
  font-size: 14px;
  display: flex;
  justify-content: start;
  align-items: center;
  align-content: center;
}

.VuePagination__container{
  margin-top: 25px;
  nav{
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
}
.pagination.VuePagination__pagination{

  display: flex;
  list-style-type: none;
  margin:0px;
  padding: 0px;
}
.VuePagination__count {
  margin-left: 10px;
  color: #B0B3C1;
}
.VuePagination__pagination-item.page-item.active{
  a{
    color: #525252;
  }
}
.VuePagination__pagination-item.page-item{
  padding: 0px;
  min-width: 32px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
  border-radius: 5px;
  border: 1px solid #EAEBEF;
  margin-right:5px;
  &:last-child{
    margin-right: 0px;
  }
  a{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    transition: 0.3s;
    text-decoration: none;
    color:#B0B3C1;
    font-size: 12px;
    &:hover{
      color: #525252;
    }
  }
}

</style>



