<template lang="pug">

  .field.field-items
    label.field__label {{ model.label }}
      .field__container(v-for="item in model.value")
        input.field__input(v-model="item.value" placeholder="add new item")
        .field__destroy(@click="destroy(item)") 
          img(src="@/assets/icons/cross-out.svg")
      .field__add(@click="add") {{'+ Add pool url'}}
    .field__errors {{ model.errors }}
</template>

<script>
export default {
  props:['model'],
  created(){
    this.model.value = this.model.value || []
  },
  methods:{
    add(option){
      option = option || {value}
      this.model.value.push(option)
    },
    destroy(item){
      this.model.value = this.model.value.filter(option=>option!==item)
    }
  }
}
</script>

<style lang="scss">
.field.field-items,.field__container{
  width: 100%
}
.field__container{
  position: relative;
  margin-bottom: 6px;

}
.field__add{
  cursor: pointer;
  margin-top: 15px;
  padding: 12px 0px;
  display: flex;
  user-select: none;
  color:#EA600F;
  justify-content: flex-end;
}
.field-items{
  .field__destroy{
    position: absolute;
    top:0px;
    cursor: pointer;
    width: 20px;
    right: 5px;
    height: 100%;
    display: flex;
    align-items: center;
    img{
      width: 50%;
      height: 50%;
      object-fit: contain
    }
  }
  .field__input{
    margin-bottom: 0px;
    padding-right: 30px;
    width: 100%
  }
}
</style>



