<template lang="pug">
  page.settings
    page-main
      card.no-password-specified(v-if="notPassword")
        .no-password-specified__title
          | Config editor is unavailable. Please specify "webPassword" parameter in config and restart the miner to be able to edit miner's config.
      card.not-logged-in(v-if="!session")
        .not-logged-in__title
          | Config editor is unavailable. Please log in using the form at the top right of the screen.
      card(title="settings" v-if="isPasswordSetAndSession")
        card.paper
          .left-side
            textbox(:model="form.configarea")
          div(style="width:100%")
            Button(label="Save & apply changes" :fuild="false" @click.native="onSave" style="float:left")
</template>

<script>
  import Finminer from '@/services/finminer'
  import FormData from '@/utils/formData'
  export default {
    data(){
      return {
        form:this.initialValues(),
      }
    },
    computed:{
      session(){
        return this.$store.state.app.session.actived
      },
      isPasswordSetAndSession(){
        return this.$store.state.app.session.password != '' && this.session
      },
      notPassword(){
        return this.$store.state.app.session.password == '' && this.session
      }
    },
    methods:{
      initialValues(){
        const Form = Finminer.settings.form()
        const conf = this.$store.state.app.miners.config

        const change = (field,value) =>{
          if(field.options){
            const filtered = field.options.filter(option=>option.value == value)
            field.value = filtered.length?filtered[0]:null
          } else {
            field.value = value
          }

        }

  

        if(conf.length){          
          change(Form.configarea, conf)
        }

        return Form
      },

      async onSave(){
        await this.$store.dispatch('app/settings/save',this.form)
        // this.form = this.initialValues()
      }
    }
  }
</script>

<style lang="scss">
.page.settings{
  .page__main{
    flex-direction: column
  }
  .card{
    width: 100%;
    .card.paper{
      padding:32px 25px 31px 25px;
      width: 100%;
      .card__main{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
      }
    }
    .left-side,.right-side{
      flex-basis: 98%;
      min-width: 200px;
    }
  }
}
</style>



