<template lang="pug">
  page.mining
    page-main
      card.stats(title="Miners")
        table-manager(v-if="machines" :props="table" :data="machines")
          template(v-for="machine in machines" slot="address" scope="{item}")
            a.link(:href="item") {{item}}
        card.machines-list-empty(v-if="!machines")
          .machines-list-empty__title
            | No other miners were found
</template>

<script>
import { mapState } from 'vuex'
export default {
  computed:mapState({
    machines:state => state.app.miners.machines
  }),
  data:()=>({
    table:{
      headers:{
        address:'address',
        hashrate:'hashrate',
        name:'rigName',
      },
    }
  }),
}
</script>

<style lang="scss">
.page.mining{
  .machines-list{
    width: 100%;
    height: auto;
    overflow-y: auto;
    display: flex;
    flex-wrap: wrap;
     justify-content: flex-start;
    .machine{
      flex:0 0 calc(50% - 10px);
      margin-right: 20px;
      cursor: pointer;
      &:nth-child(2n){
        margin-right: 0px;
      }
      padding: 0px;
      &:hover{
        box-shadow: 1px 1px 5px rgba(0,0,0,0.2);
        opacity: 0.9
      }
      transition: all 0.3s ;
      box-shadow: 1px 1px 2px rgba(0,0,0,0.1);
      .card__main{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
      }
      &-indicator{
        width: 5px;
        height: 5px;
        border-radius: 50px;
        background-color: rgba(20,255,150,1);
        box-shadow: 1px 1px 5px rgba(0,255,100,0.9);
        margin: 12px
      }
      &-name{
        font-weight:500;
        padding:12px;
      }
      &-address{
        padding:12px;
      }
      &-hashrate{
        padding:12px;

      }
    }

    &-empty{
      display: flex;
      align-items: center;
      justify-content: center;
      align-content: center;
      width: 100%;
      height: 35vh;
      &__title{
        font-size: 16px;
        opacity: 0.7;

      }
    }
  }
  .card.stats{
    width: 100%;
    .card__main{
      .currencies{
        display: flex;
        justify-content: space-between;
      }
      .list{
        width: 100%;
      }
    }
    .card{
      padding-bottom: 22px;
    }
    .card.a{
      flex-basis: 49.5%
    }
    .card.b{
      flex-basis: 49.5%
    }


  }
}
</style>



