<template lang="pug">
  section.card
    header.card__header(v-if="title || $slots.actions")
      h1.title(v-if="title || $slots.actions") {{title}}
      slot(name="actions")
    main.card__main
      slot
    footer.card__footer(v-if="$slots.footer")
      slot(name="footer")
</template>
<script>
export default {
  props:['title','background']
}
</script>
<style lang="scss">
  .card.paper{
    padding:20px 43px 34px 43px;
  }
  .card{
    &__more{
      font-size: 12px
    }
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 20px;
    margin-bottom: 15px;
    &__footer{
      margin-top: 20px;
      .show-all{
        font-size: 12px;
      }
    }
    .card{
      margin-bottom: 0px;
      padding-bottom: 0px;
    }
    &__header{
      margin-bottom: 5px;
      min-height: 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      align-content: center;
      user-select: none;
    }
    &__content{
    }
    .title {
      white-space:nowrap;
      letter-spacing: 1pt;
      text-align: left;
      font-size: 14px;
      color:#6374B9;
      font-weight: 500;
      margin: 0px;
      text-transform: uppercase;
    }
  }
</style>



