<template lang="pug">
  .app-bar
    .app-bar-content
        navigation
        .app-bar-content__rightside
          settings
</template>
<script>
import appContainer from './../container'
import logotype from '@/components/logotype'
import navigation from './navigation'
import settings from './settings'
export default {
  components:{
    appContainer,
    logotype,
    navigation,
    settings
  }
}
</script>

<style lang="scss" >

  .app-bar{
    width: 100%;
    user-select: none;
    margin-bottom:12px;

    &-content{
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      align-content: center;
      &__rightside{
        display: flex;
        &>*{
          margin-right:50px;
          &:last-child{
            margin: 0px;
          }
        }
      }
    }
  }
</style>



