<template lang="pug">
  .signin-form(v-show="(password==false && actived)!==true")
    .signin-icon
    input.signin-field(type="password" v-model="password" v-if="!actived")
    Button(label="Login" @click.native="login" v-if="!actived")
    Button(label="Logout" @click.native="logout" v-else)
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
const sessionStore =  createNamespacedHelpers('app/session')

export default {
  mounted(){
  },
  methods:sessionStore.mapActions([
    'login',
    'logout'
  ]),

  computed: {
    ...sessionStore.mapState([
      'actived',
      'password'
    ]),
    password: {
      get () {
        return this.$store.state.app.session.password
      },
      set (value) {
        this.$store.commit('app/session/password', value)
      }
    }
  }
}
</script>

<style lang="scss">
  .signin-form{
    display: flex;
    align-items: center;
    .signin{
      &-icon{
        position: absolute;
        width:30px;
        left:6px;
      }
      &-field{
        flex: 1;
        padding: 4px;
        padding-left: 39px;
        border: 1px solid #E7E7EA;
        border-radius: 3px;
        background: white;
        font-size: 25px;
        width:  230px;
        outline: none;
      }
    }
  }
</style>



