
import finminer from '@/services/finminer'

const localPassword = localStorage.getItem('password')
export default {
  namespaced:true,
  state: {
    connection:'pending',
    actived:false,
    password:localPassword || ""
  },
  mutations: {
    connection(state,status){
      state.connection = status
    },
    actived(state,payload){
      state.actived = payload
    },
    password(state,payload){
      state.password = payload
      localStorage.setItem('password',payload)
    }
  },
  actions:{
    access(store,payload){
      const {password,connection} = store.state;
      const initConnection = connection== 'pending'

      finminer.settings.password({
        old:password,
        new:password,
        ...payload
      })
        .then(()=>{
          store.commit('actived',true)
          store.commit('connection','success')

          if(!initConnection){

            store.dispatch('app/notification',{
              group:'app',
              text:`Successfully authenticated`,
              type:'success'
            },{root:true})

          }
        })  
        .catch((e = {})=>{
          
          if(!e.response){
            store.commit('connection','error')
            localStorage.setItem('password','')
            store.dispatch('app/notification',{
              group:'app',
              type: 'error',
              text:`Application error`
            },{root:true})
          }
          if(e.response.status == '401' ||  e.response.status == '403'){
            store.commit('connection','success')

            if(!initConnection){

              store.dispatch('app/notification',{
                group:'app',
                type: 'error',
                text:`Invalid password`
              },{root:true})

            }
            store.commit('actived',false)
          }
        })
    },
    login(store,payload){
      store.dispatch('access')
    },
    logout(store,payload){
      store.commit('actived',false)
      store.commit('password','')
      localStorage.setItem('password','')
    },
    password(store,payload){
      store.commit('password',payload)
    }
  },
  getters:{

  }
}



