import txt from './txt'
import multiselect from './multiselect'
import toggle from './toggle'
import * as layoutPage from './layout/page'
import { list, listItem } from './list'
import card from './card'
import tableManager from './table-manager'
import {appBar,appContainer} from '@/components/app'
//import ProgressBar from './vue-progress-bar'
import Button from './button'
import Field from './field'
import Textbox from './textbox'

const components= { // Global
  appBar,
  appContainer,
  txt,
  Button,
  Field,
  Textbox,
  multiselect,
  card,
  toggle,
  list,
  listItem,
  tableManager,
//  ProgressBar,
  ...layoutPage
}


const Plugin = {
  install:(Vue, options)=>{
    for (let namespace in components) {
      if (components.hasOwnProperty(namespace)) {
        let component = components[namespace]
        let { name } = component
          Vue.component(namespace,component)
      }
    }
  }
}

export default Plugin



