import Finminer from '@/services/finminer'
import storage from '@/storage'
import moment from 'moment'

export default {
  namespaced:true,
  state: {
    config:'',
    log:'',
    machines:[],
    command:null,
  },
  mutations: {
    log(state,log){
      state.log = log
    },
    config(state,config){
      state.config = config
    },
    command(state,command){

    },
    machines(state,machines){
     state.machines = machines
    },
  },
  actions: {
    log(store){
   
      return Finminer.log()
        .then(({data}) => {
       
          store.commit('log',data)
          return response
        })
        .catch(()=>{

        })
    },
    config(store){
      return Finminer.miners.config()
        .then(({data}) => {
          store.commit('config',data)
          return response
        })
        .catch(()=>{
        })
    },

    scanning:({commit}) => Finminer.scan()
      .then( response => commit('machines',response.data.result) || response ),

    commands:(store,{command}) => Finminer.commands({command})
      .then( response => {
        store.commit('command',response.data.result)

        const actions = {
          restart:'restarted',
          stop:'stoped',
          start:'started'
        }
        store.dispatch('app/notification',{
          group:'app',
          text:`Successfully ${actions[command]}`
        },{root:true})

        return  response
      })
      .catch((e)=>{
        store.dispatch('app/notification',{
          group:'app',
          type: 'error',
          text:`You need to set the password and authorize to restart the miner`
        },{root:true})

        return e
      })

  },
  getters:{

  },

}



