<template lang="pug">
  chart-line(:chart-data="chartData" :options="options")
</template>

<script>
// CommitChart.js
import flatColor from '@/utils/flatColor'
import ChartLine from '@/components/chart-line'
import moment from 'moment'

const colors = [
  '#e6194b',
  '#3cb44b',
  '#ffe119',
  '#4363d8',
  '#f58231',
  '#911eb4',
  '#46f0f0',
  '#f032e6',
  '#bcf60c',
  '#fabebe',
  '#008080',
  '#e6beff',
  '#9a6324',
  '#fffac8',
  '#800000',
  '#aaffc3',
  '#808000',
  '#ffd8b1',
  '#000075',
  '#808080',
  '#ffffff',
  '#000000'
]

const axisTimeFormat = 'HH:mm'
const labelTimeFormat = 'dddd DD MMM HH:mm'
const options = {
  
  maintainAspectRatio:false,
  title:      {
      display: false,
      //text:    "Chart.js Time Scale"
  },
  tooltips: {

    callbacks: {
      label: (tooltipItem, data) => {
        const item = data.datasets[tooltipItem.datasetIndex].data[tooltipItem.index];
        return item.label
      }
    }
  },
  scales:     {
      xAxes: [{
          type: "time",
          time:{
              unit: "minute",
              displayFormats: {
                'minute': axisTimeFormat
              },
              tooltipFormat: labelTimeFormat
          },
          scaleLabel: {
              display:     true,
              labelString: 'Time'
          }
      }],
      yAxes: [{
          ticks: {
            beginAtZero: true
          },
          scaleLabel: {
            display:     true,
            labelString: 'Temperature'
          }
      }]
  }
}



export default {
  props:['data'],
  components:{
    ChartLine
  },
  data(){
    this.options = options
    return {
      chartData:null
    }
  },
  methods:{
    dataMap(){
      if(!this.data)
        return 
        
        const createDatasets = (sets)=>{
        const datasets = []
        const createSet = (label,data,index)=>({
          label,
          borderColor:colors[index],
          backgroundColor:'rgba(0,0,0,0)',
          data: data.map((item) => {
            var labelData = 
              [ 
                `Name: ${item.name}`,
                `Hashrate: ${item.hashrates[0].hashrate} ${item.hashrates[0].unit}`
              ];
            if (item.temp > 0) {
              labelData.push(`Temperature: ${item.temp}`);
            }
            if (item.fan > 0) {
              labelData.push(`Fan: ${item.fan}`);
            }
            if (item.power.length) {
              labelData.push(`Power: ${item.power}`);
            }
            return {
              x:moment(item.timestamp),
              y:item.temp,
              label:labelData
              }
            }),
            pointRadius: 5,
            pointHoverRadius: 7,
            cubicInterpolationMode: 'monotone'
          })
          function compare(a,b) {
            if (a.id < b.id)
              return -1;
            if (a.id > b.id)
              return 1;
            return 0;
          }
          for (var set in sets) {
            if (sets.hasOwnProperty(set)) {
              sets[set].sort(compare)
              var index = -1
              var lastId = "-1"
              for (var subset in sets[set]) {
                const label = sets[set].length?sets[set][subset].id:set
                if (lastId != sets[set][subset].id) {
                  index++
                  var filteredSubset = sets[set].filter(
                    function (item) {
                      return item.id == sets[set][subset].id && item.id !== "CPU";
                    }
                  );
                  if (filteredSubset.length) {
                    datasets.push(createSet(label,filteredSubset,index))
                  }
                }
                lastId = sets[set][subset].id
              }
            }
          }

          return datasets
        }
      const data = {
        datasets: createDatasets(this.data)
      }
     return data
    }
  },
  mounted(){
    this.chartData = this.dataMap()
  },
  watch:{
    data(){
      this.chartData = this.dataMap()
    }
  }
}
</script>



