<template lang="pug">
  items(v-if="model.type=='list'" :model="model")
  .textbox(v-else-if="!model.options")
    label.textbox__label {{ model.label }}
    textarea.textbox__input(
      :value="model.value"
      @input="change"
      :placeholder="model.placeholder"
      )
    .textbox__errors {{ model.errors }}

  .textbox(v-else-if="model.options")
    label.textbox__label {{ model.label }}
    multiselect(
      v-model="model.value"
      track-by="value",
      label="label"
      placeholder="Select one"
      :options="model.options"
      :close-on-select="true"
      :show-labels="false"
      :searchable="false"
      :allow-empty="false"
    )
      template(slot="singleLabel", slot-scope="{ option }")
        div {{ option.label }}
    .textbox__errors {{ model.errors }}
</template>


<script>
  import Multiselect from '@/components/multiselect'
  import items from './items'
  const typesPatterns={
    textareapattern:/\t/,
  };

  export default {
    props:['model'],
    components:{
      Multiselect,
      items,

    },
    created(){
      this.patterns = typesPatterns
    },

    methods:{
      change(e){
        let {value} = e.target
        let string  = new String(value)
        const pattern = typesPatterns[this.model.type];
        if(pattern){
          string = string.replace(pattern, '')
         }
        e.target.value = string
       this.model.value = string
      }

    },
    computed:{

    }
  
  }
</script>

<style lang="scss">
  @import '@/assets/scss/multiselect.scss';

.textbox{
  display: flex;
  flex-direction: column;
  &__input{
    font-family: 'Muller', sans-serif;
    font-weight: 500;
    border-radius: 3px;
    border: 1px solid #E7EBF0;
    font-size: 14px;
    min-height: 67vh;
    padding:  6px 12px 6px 12px;
    outline: none;
    color:#525252;
    padding: 12px;
    margin-bottom: 6px;
    &::placeholder{
      user-select: none;
      font-size: 14px;
      color:#525252;
      opacity: 0.5;
      font-weight: 500;
    }
  }
  &__errors{
    font-weight: 500;
    margin-bottom:  8px;
    color:#FC5830;
  }
  &__label{
    font-weight: 500;
    margin-bottom: 8px;
    font-size: 14px;
    color: #848484
  }
}
</style>



