const isObject = (prop)=>typeof prop == 'object' && prop!==null
const option = ({form,data,prop})=>{
  if(isObject(form[prop].value)){
    const option = form[prop].value
      data[prop] = option.value
  }else{
    data[prop] = form[prop].value
  }
}
const tree = (form,data)=>{
  // recursive mutation data from form properties
  // section:{ property : {value:'' } } => section.property = ''
  // property:{ value:'' } => property:''

  for (var prop in form) {
    if(isObject(form[prop])){
      if (form[prop].hasOwnProperty('value')) {

        if(Array.isArray(form[prop].value)){
          data[prop]=form[prop].value.map(item=>item.value)
        }else{
          option({form,data,prop})
        }


      }else{
        data[prop] = {}
        tree(form[prop],data[prop])
      }
    }else if(Array.isArray(form[prop])){

    }
  }
}


export default (form)=>{
  const data = {}
  tree(form,data)
  console.log(data);
  return data
}



