<template  lang="pug">
  #app
    drawer-layout(
      ref="drawer"
      :drawer-width="300"
      :enable="false"
      :animatable="true"
      :z-index="1000"
      :drawable-distance="Math.floor(800/3)"
      :content-drawable="false"
      :backdrop="false"
      :backdrop-opacity-range="[0,0.4]"
  
      )
      .drawer-content(
          slot="drawer"
        )
        Navigation
          NavItem(label="Mining" to="mining")
            img(slot="icon" src="@/assets/icons/arrowup.svg")
          NavItem(label="Rig" to="rig")
            img(slot="icon" src="@/assets/icons/chart-bar.svg")
          NavItem(label="Config editor" to="settings")
            img(slot="icon" src="@/assets/icons/joystick.svg")
          NavItem(label="Local miners" to="scanning")
            img(slot="icon" src="@/assets/icons/joystick.svg")

      .content(slot="content")
        transition(name="component-fade" mode="out-in")
          connection(v-if="connection!=='success'")
          app-container(v-else)
            app-bar
            transition(name="component-fade" mode="out-in")
              router-view
    
    modal.modals( 
      name="log"
      resizable
      draggable
      height="80%"
      width = "70%"
      @opened="whenOpened"
    )
      .log 
        pre {{log}} 

    notifications(group="app")

</template>
<script>
import {DrawerLayout} from 'vue-drawer-layout'
import {Navigation,NavItem} from '@/components/navigation'
import { appBar,appFooter, appContainer } from '@/components/app'
import connection from '@/components/connection'
export default {
  name: 'app',
  components: {
    DrawerLayout,
    Navigation,
    NavItem,
    appBar,
    appContainer,
    appFooter,
    connection,
  },
  data:()=>({

  }),
  mounted(){
    this.$refs.drawer.toggle(true)
  },
  methods:{
    whenOpened (event) {
      var logElem = document.getElementsByClassName("log");
      logElem[0].scrollTop = logElem[0].scrollHeight;
    },
  },
  watch:{
    '$store.state.app.notification'(params){
      if(params.group == "app")
        this.$notify(params)
    }
  },
  computed:{
    log(){
        return this.$store.state.app.miners.log
    },
    connection(){
      return this.$store.state.app.session.connection
    }
  }
}
</script>

<style lang="scss">
@import 'reset-css';

.drawer-content{
  background-color: #393946;
  height: 100vh;
  overflow: hidden;
}
.content{
  margin-left: 300px;
}
#app {
  font-family: 'Muller', Arial, sans-serif;
  font-weight:500;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #525252;
  min-height: 100vh;
  font-size: 14px;
  background-color: #f5f6fa;
  overflow: hidden;


}
html, body {
  margin:0px;

}
body{
  overflow: hidden;
}
*, :after, :before {
    box-sizing: border-box;
}
a{
  outline: none;
}
button{
  cursor: pointer;
  outline: none;
  user-select: none
}

.modals,.v--modal-overlay{
  z-index: 9999 !important;
    .log{
      color:#b6b6b6;
      background: black;
      font-family: 'Courier New', Courier, monospace;
      user-select: text;
      width:100%;
      padding: 12px;
      letter-spacing: 1px;
      line-height: 20px;
      font-size: 12px;
      overflow-y: scroll;
      overflow-x: none;
      max-height: 100%;
      pre{
        white-space: pre-wrap;
        word-wrap: break-word;
      }
    }
  .v--modal{
    box-shadow: none;
  }
}
.appMain{
  min-height: 70vh
}
.component-fade-enter-active, .component-fade-leave-active {
  transition: opacity .3s ease;
}
.component-fade-enter, .component-fade-leave-to
/* .component-fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.paper{
  border-radius: 8px;
  background: white;
  border: 1px solid #EAEBEF;
}
.text-overflow{
  display:block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.divider-vertical{
  position: relative;
  width: 100%;
  padding-right: 5%;
  margin-left: 5%;
  content: '';
  &:first-child{
    margin-left:0%;
  }
  &:last-child{
    padding-right: 0%;
    margin-right: 0%;
    &:after{
      display:none
    }
  }
  &:after{
    display: block;
    content: '';
    height: 90%;
    width: 1px;
    background: #d6d6d6;
    position: absolute;
    top: 5%;
    right:0%;

  }
}
</style>



