<template lang="pug">
  page.mining
    page-header
      card.stats(title="Mining")
        .currencies(v-for="currency in currencies")
          card.paper.a
            list
              listItem(name="Coin" :value="currency.coin")
              listItem(name="Wallet" :value="currency.wallet")
              listItem(name="Pool Address" :value="currency.pool")
              listItem(name="Rig Name" :value="currency.rigName")
          card.paper.b
            list
              listItem(name="Current hashrate" :value="hashrate(currency.hashrate)+' '+currency.unit")
              listItem(name="Total shares" :value="currency.accepted+currency.denied")
              listItem(name="Rejected" :value="currency.denied")
              listItem(name="Uptime" :value="time(currency.workTime)")
    page-main(v-if="devices" v-for="currency in currencies")
      card.stats(:title="currency.coin")
        card.paper.a
          chart.chart(:data="devices" :coin="currency.coin")
</template>

<script>
import moment from 'moment'
import { mapState } from 'vuex'
import chart from './chart'
export default {
  components:{
    chart
  },
  created(){
    this.moment = moment
  },
  methods:{
    time(t){
      const now = Date.now()
      const howLongAgo = moment(now - t*1000).fromNow()
      return howLongAgo.substring(0, howLongAgo.length - 4);
    },
    hashrate(value){
      return value.toFixed(2)
    }
  },

  computed:mapState({
    devices:state => state.app.statistic.last.devices,
    currencies:state => state.app.statistic.currencies,
    miners:state => state.app.miners.config
  }),
}
</script>

<style lang="scss">
.page.mining{
  .card.stats{
    width: 100%;
    .card__main{
      .currencies{
        display: flex;
        justify-content: space-between;
      }
      .list{
        width: 100%;
      }
    }
    .card{
      padding-bottom: 22px;
    } 
    .card.a{
      flex-basis: 49.5%
    }
    .card.b{
      flex-basis: 49.5%
    }


  }
}
</style>



