import Finminer from '@/services/finminer'
import formToData from  '@/services/finminer/data'
//import initialValues from './form'

export default {
  namespaced:true,
  state:{

  },
  mutations:{

  },
  actions:{
    async save(store,payload){
      const {
        configarea,
      } = formToData(payload);

       return Finminer.settings.save(configarea)
        .then((res)=>{
          store.dispatch('app/notification',{
            group:'app',
            text:`Successfully updated miner configuration`,
            type:'success'
          },{root:true})
          return res
        })
        .catch((e)=>{
          if(e.response.status == '401' ||  e.response.status == '403'){
            store.dispatch('app/notification',{
              group:'app',
              text:`Failed to update, requires authentication`,
              type:'error'
            },{root:true})
            return e
          }
          store.dispatch('app/notification',{
            group:'app',
            text:`Failed to update miner configuration`,
            type:'error'
          },{root:true})
          return e
        })

    
    },

    async load(store,payload){
      const {data, errors} = await Finminer.miners.load()
      store.commit('config',response.data.simple)
      return {data, errors}
    },

  },
}



