<template lang="pug">
  .navigation.settings
    .navigation__item
      signin
</template>

<script>
import signin from '@/components/sign-in'
  export default {
    components:{
      signin
    },
    created(){

    },
    data () {
      return {

      }
    },
    methods:{

    },
    computed:{

    }
  }
</script>
<style lang="scss">

.settings{
  min-width: 232px;
}
.navigation {

  display: flex;
  justify-content: space-around;
  align-items: center;
  &__item{
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-transform: uppercase;
    font-size: 14px;
    padding:15px;
    letter-spacing: 0.5px;
    font-weight: 500;
    text-decoration: none;
    color: #626262;
    .multiselect{

      color: inherit;
      font-size: inherit;
      &.language{
        .multiselect__content-wrapper{
          right: 35px !important;
        }
      }
      &__content-wrapper{
        right: 0px;
        width: auto;
        margin-top: 0px;
        border: none;
        border-radius: 5px;
        box-shadow: 0px 6px 20px rgba(116, 116, 163, 0.22);
      }
      &__content{
        display: flex !important;
      }
      &__element{

      }
      &__select{

        width: 26px;
        padding: 7px 8px;
      }
      &__tags{
        font-size: inherit;
        padding: 12px 26px 0 0px;
        border:none ;
      }
      &__single{
        font-size:  14px;
      }
      &__option{
        color:#B0B3C1;
        text-transform: uppercase;
        &--highlight{
          background: none!important;
          color: #5DC79C
        }
        &--selected{
          color: #626262;
          font-weight: 500;
          background: none!important;
          &.multiselect__option--highlight{
              background: none!important;
          }
        }

      }
    }

  }


}

</style>



