import form from './form'
import http from '../http'
import toHash from '@/utils/toHash'
export default {
  form,
  save:(data)=>http({
    method: 'put',
    url: 'config',
    data,
  }),
  load:(form)=>http({
    method: 'get',
    url: 'config',
  }),
  password:(props)=>http({
    method: 'put',
    url: 'pass',
    data:{
      old:props.old?toHash(props.old):props.old,
      new:props.new?toHash(props.new):props.new
    }
  })
 }



