<template lang="pug">
  footer.app-footer
    appContainer.app-footer__container
</template>

<script>
import {appContainer} from '@/components/app'
import logotype from '@/components/logotype'
export default {
  components:{
    appContainer,
    logotype
  }
}
</script>

<style lang="scss">
.app-footer{
  &__container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
  }
  &__col{

  }
  &__item{
    color: #949496;
    margin-bottom: 11px
  }
  padding-top: 35px;
  padding-bottom: 45px;
  margin-top: 10px;
  width: 100%;


  border-top: 1px solid #e8e8e8;
}
</style>



