
<template lang="pug">
  span {{message}}
</template>

<script>
//import {Translate} from '@/services/multi-language'

export default {
  name:'txt',
  props:['to'],
  computed:{
    message(){
      const nodes = this.$slots.default
      if(nodes.length == 0){
        console.error('Translate error: not found message in default slot in txt');
        return 'Error translate'
      }
      if(!nodes[0].text){
        return 'Error translate'
      }
      if(!this.to){
        console.error('Translate error: not found property "to" in txt')
        return 'Error translate'
      }
      const language = this.$store.state.app.language
      const message = nodes[0].text.toUpperCase()
        return Translate(language,this.to,message)

    }
  }
}


</script>

<style lang="css">
</style>



