<template lang="html">
  <div class="toggle" >
    <input class="reversed" type="checkbox" :checked="checked" id="toggle" />
		<label @click="click" for="toggle">Toggle</label>
  </div>
</template>

<script>
export default {
  props:['checked','click']
}
</script>

<style lang="scss">
.toggle{
  display: flex;
  align-items: center;
  justify-content: center;


	input[type=checkbox]{
		height: 0;
		width: 0;
		visibility: hidden;
		margin: 0px;
	}

	label {
		cursor: pointer;
		text-indent: -9999px;
		width: 28px;
		height: 13px;
		background: #B0B3C1;
		display: block;
		border-radius: 100px;
		position: relative;
	}

	label:after {
		content: '';
		position: absolute;
		top: 1px;
		left: 1px;
		width: 11px;
		height: 11px;
		background: #fff;
		border-radius: 90px;
		transition: 0.3s;
	}

	input:checked + label {
		background: #6166B6;
	}


	input:checked + label:after {
		left: calc(100% - 1px);
		transform: translateX(-100%);
	}

	input.reversed:checked + label:after {
		left: 1px;
		transform: translateX(0%);
	}
	input.reversed + label:after {
		left: calc(100% - 1px);
		transform: translateX(-100%);
	}

	label:active:after {
		width: 15px;
	}
}
</style>



