<template lang="pug">
  li.navItem
    router-link.link(:to="{name:to}")
      .navItem__icon
        slot(name="icon")
      .navItem__label {{label}}
</template>

<script>
export default {
  props:['icon','label','to']
}
</script>

<style lang="scss">
  .navigation{
    .link.router-link-exact-active{
        box-shadow: inset 8px 0px 0 #C57C0F;
      //  animation-duration: s;
        // -webkit-animation: link-exact-active 2s  ;
        // -moz-animation: link-exact-active 2s  ;
        // animation: link-exact-active 2s  ;
        // background-position: 0% 0%
        background-color: rgba(125,185,232,0.05);
      }


    .navItem,.link{
      text-decoration: none;
      color: white;
      width: 100%;
      display: flex;
      list-style: none;
      margin: 0px;
      //padding-left: 8px;
      align-items: center;
      align-content: center;
      justify-content: flex-start;
      height: 60px;
      box-shadow: inset 0px 0px 0 #C57C0F;

      // background: linear-gradient(
      //   to left,
      //   rgba(30,87,153,0) 0%,
      //   rgba(125,185,232,0.1) 100%
      // );
    //  background-size: 200% 0%;
    //  background-position: 100% 0%;
     transition: all 0.3s ease;
      cursor: pointer;
        &__icon{
        margin-left: 28px;
        margin-right: 12px;
        width: 16px;
        height:16px;
      }
      &__label{
        font-size: 14px;
        text-transform: uppercase;
        font-weight: bold;
        letter-spacing: 1px;
        opacity: 0.8;
        color: white;
        transition: 0.3s;
        &:hover{
          opacity: 1;
        }
      }
    }
  }

  @keyframes link-exact-active {
    from {
      background-position: 100% 0%;
    }

    to {
      background-position: 0% 0%
    }
  }
</style>



