import http from '@/services/finminer/http'

export default {
  current: ()=> http({
    method: 'get',
    url: 'stat',
  }).then(({data:{Statistics}})=>{
    return Statistics
  }),

  last:()=>http({
    method: 'get',
    url: 'last',
  }).then(({data:{stats}})=>{
    if(!stats){
      return false
    }
    const Devices = {};
    const Currencies = {};
    stats.forEach(shot=>{
      const { timestamp } = shot
      shot.Devices.forEach(device=>{
        if(!Devices.hasOwnProperty(device.uuid)){
          Devices[device.uuid] = []
        }
        Devices[device.uuid].push({...device,timestamp})
      })

      shot.Currencies.forEach(currency=>{
        if(!Currencies.hasOwnProperty(currency.coin)){
          Currencies[currency.coin] = []
        }
        Currencies[currency.coin].push({...currency,timestamp})
      })

    })
    return {
      devices:Devices,
      currencies:Currencies
    }
  }),

  list:()=> http({
    method: 'get',
    url: 'stat',
  }).then(({data:{stats}})=>stats),

  info:()=> http({
    method: 'get',
    url: 'device',
  }).then(({data:{devices}})=>{
    return devices
  }),
}



