
const prefix = "Tabtrader"
const Storage = {
  getItem:(name,data)=>{

    const res = localStorage.getItem(prefix+':'+name)
    if(res)
      return JSON.parse(res)

      else return null
  },
  setItem:(name,data)=>{
    localStorage.setItem(prefix+':'+name,JSON.stringify(data))
  }
}
export default Storage



